<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl">
                    <p class="balance"><span>余额</span></p>
                    <div class="ba-box ov">
                        <p class="fl balance-b"><span>余额</span> <i>{{walletData.money}} </i></p>
                        <div class="fr tx-btn ov">
                            <p class="fl"><img src="@/static/images/tixian.png" width="25" height="22" alt=""><span @click="withdrawDialogVisible = true">提现</span></p>
                            <p class="fl"><img src="@/static/images/tixian.png" width="25" height="22" alt=""><span @click="transferDialogVisible = true">内部转账</span></p>
                            <p class="fl"><img src="@/static/images/tixian.png" width="25" height="22" alt=""><span @click="flashbuy">人民币充值提现USDT</span></p>
                        </div>
                    </div>
                    <p class="balance"><span>地址</span></p>
                    <div class="ba-box ov">
                        <p class="fl balance-b" style="margin-top: 24px;"><span>{{walletData.address}}</span><br><br><span style="color: red">注意:本地址只接受erc20版本的usdt，请勿充值其它币种，否则无法找回</span></p>
                        <div class="fr tx-btn ov">
                            <img :src="walletData.address_qrcode" class="fl" alt="" style="width: 70px;height: 70px;background: #333;display: inline-block;">
                            <p class="fl copy" @click="copy(walletData.address)"><img src="@/static/images/tixian.png" width="25" height="22" alt=""><span >复制地址</span></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <el-dialog
                title="提现"
                :visible.sync="withdrawDialogVisible"
                width="30%"
                center>
            <el-form ref="withdrawForm" :rules="withdrawrules" :model="withdrawForm" label-position="left" label-width="120px" style="">

                <el-form-item label="地址" prop="address">
                    <el-input v-model="withdrawForm.address" />
                </el-form-item>
                <el-form-item label="金额" prop="money">
                    <el-input v-model="withdrawForm.money" />

                </el-form-item>
                <el-form-item label="">
                    <div class="el-block-help"><span style="float: left">手续费:{{serviceMoney}}</span><span style="float: right">实际到账:{{realMoney}}</span></div>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="withdrawDialogVisible = false">取 消</el-button>
    <el-button type="primary" :disabled="withdrawdisable" @click="submitWithdraw">确 定</el-button>
  </span>
        </el-dialog>

        <el-dialog
                title="转账"
                :visible.sync="transferDialogVisible"
                width="30%"
                center>
            <el-form ref="transferForm" :rules="transferrules" :model="transferForm" label-position="left" label-width="120px" style="">

                <el-form-item label="对方账号" prop="username">
                    <el-input v-model="transferForm.username" />
                </el-form-item>
                <el-form-item label="金额" prop="money">
                    <el-input v-model="transferForm.money" />
                </el-form-item>
                <el-form-item label="">
                    <div class="el-block-help">转账的账号必须是用户登录的账号</div>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="transferDialogVisible = false">取 消</el-button>
    <el-button type="primary" :disabled="transferdisable" @click="submitTransfer">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import { validMoney } from "../../utils/validate";
    import {wallet, withdrawInfo, withdraw, transfer} from "../../api/center";
    import fmath from "../../utils/math"
    import Clipboard from 'clipboard'
    export default {
        name: "Center",
        components: {
            Navbar,
            Centerbar
        },
        data(){
            var checkWithMoney = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('提现金额不能为空'));
                }
                if (!validMoney(value)) {
                    callback(new Error('请输入合法金额'));
                } else {
                    if (value <= 0 || (value < this.withdrawInfo.cash_min && this.withdrawInfo.cash_min > 0)) {
                        callback(new Error('提现金额必须大于 '+this.withdrawInfo.cash_min));
                    } else if (value > this.withdrawInfo.cash_max && this.withdrawInfo.cash_max > 0){
                        callback(new Error('提现金额最大为 '+this.withdrawInfo.cash_max));
                    }  else if (value > this.walletData.money){
                        callback(new Error('余额不足'));
                    }  else {
                        callback();
                    }
                }
            }
            var checktmoney = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('提现金额不能为空'));
                }
                if (!validMoney(value)) {
                    callback(new Error('请输入合法金额'));
                } else {
                    if (value < 1) {
                        callback(new Error('提现金额必须大于 1'));
                    } else if (value > this.walletData.money){
                        callback(new Error('余额不足'));
                    } else {
                        callback();
                    }
                }
            }
            return {
                withdrawdisable:false,
                transferdisable:false,
                walletData:{},
                withdrawDialogVisible:false,
                transferDialogVisible:false,
                withdrawForm:{
                    address:'',
                    money:''
                },
                transferForm:{
                    username:'',
                    money:''
                },
                withdrawInfo:{
                    cash_service_ratio: 0,
                    cash_service_max: 0,
                    cash_min: 0,
                    cash_max: 0,
                },
                serviceMoney:0,
                realMoney:0,
                withdrawrules: {
                    address: [{ required: true, message: '地址不能为空', trigger: 'blur' }],
                    money: [{required: true, validator: checkWithMoney, trigger: 'blur' }],
                },
                transferrules: {
                    username: [{ required: true, message: '账号信息不能为空', trigger: 'blur' }],
                    money: [{required: true, validator: checktmoney, trigger: 'blur' }],
                }
            }
        },
        watch:
            {     // 计算手续费及到账费用
                withdrawForm:
                    {
                        handler(val,oldval){

                            let ratio = this.withdrawInfo.cash_service_ratio / 100
                            let smoney = fmath.floatMul(ratio, this.withdrawForm.money)
                            if (this.withdrawInfo.cash_service_max > 0 && smoney > this.withdrawInfo.cash_service_max)
                            {
                                this.serviceMoney = this.withdrawInfo.cash_service_max
                            } else
                            {
                                this.serviceMoney = smoney
                            }

                            this.realMoney = fmath.floatSub(this.withdrawForm.money, this.serviceMoney)

                        },
                        immediate:false,
                        deep:true
                    },
            },
        mounted()
        {
            this.getWallet()
            this.getWithdrawInfo()
        },
        methods:{

            getWallet()
            {
                wallet().then(res => {
                    this.walletData = res
                })
            },
            // 获取提现设置
            getWithdrawInfo()
            {
                withdrawInfo().then(res => {

                    this.withdrawInfo = res
                })
            },
            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    this.$message({
                        message: '复制成功',
                        type: 'success'
                    });
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    this.$message({
                        message: '复制失败, 当前浏览器不支持',
                        type: 'error'
                    });
                    // 释放内存
                    clipboard.destroy()
                })

            },
            // 去闪币付款
            flashbuy()
            {
                let url = 'https://buy.flashbuy.io/#/trade?name=usdt-erc20&cny=0&address='+this.walletData.address
                window.open(url);
            },
            // 提现
            submitWithdraw(){
                this.$refs['withdrawForm'].validate((valid) => {
                    if (valid) {
                        this.withdrawdisable = true
                        withdraw(this.withdrawForm).then(res => {
                            this.withdrawdisable = false
                            this.withdrawDialogVisible = false
                            this.walletData.money = res.balance
                            this.$notify({
                                title: '提现通知',
                                message: '提现成功',
                                type: 'success'
                            });
                        })
                    }
                })
            },

            // 转账
            submitTransfer(){
                this.$refs['transferForm'].validate((valid) => {
                    if (valid) {
                        this.transferdisable = true
                        transfer(this.transferForm).then(res => {
                            this.transferdisable = false
                            this.transferDialogVisible = false
                            this.walletData.money = res.balance
                            this.$notify({
                                title: '转账通知',
                                message: '转账成功',
                                type: 'success'
                            });
                        })
                    }
                })
            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/center.css";
     .box {height: 100%;background: #F2F4F9;}
     .el-block-help {
         color: #C0C4CC;
         width: 100%;
     }
</style>



